let id = 0
function createData (name, calories, fat, carbs, protein) {
  id += 1
  return { id, name, calories, fat, carbs, protein }
}

const rows = [
  createData('io.tulip.sat.products', 'Y', null, null, 'Edit'),
  createData('io.tulip.sat.customers', 'Y', null, null, 'Edit'),
  createData('io.tulip.associates', 'Y', null, null, 'Edit'),
  createData('io.tulip.stores', 'Y', null, null, 'Edit'),
  createData('io.tulip.sat.stores', 'N', null, null, 'Edit')
]

const links = [
  {
    label: 'Application Modules',
    link: '/clienteling/appModules'
  },
  {
    label: 'Main Menu Items',
    link: '/clienteling/mainMenuItems'
  },

  {
    label: 'App Views',
    link: '/clienteling/appViews'
  },

  {
    label: 'App Variables',
    link: '/clienteling/appVariables'
  },

  {
    label: 'Management Center Configuratrions',
    link: '/clienteling/managementCenterConfigurations'
  }
]

function Applications (props) {
  const { classes } = props

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false
  })

  const [values, setValues] = React.useState({
    enabled: true,
    featureID: 'io.tulip.sat.customers',
    name: 'Customers',
    category: '',
    type: '',
    path: ''
  })

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value })
  }

  const openEditPanel = () => () => {
    setState({ ...state, left: false })
    setState({ ...state, right: true })
    console.log('doc = ' + state.left)
    console.log('edit = ' + state.right)
  }

  const toggleDrawer = (side, open) => () => {
    setState({ ...state, [side]: open })
    console.log('doc = ' + state.left)
    console.log('edit = ' + state.right)
  }

  const toggleDocDrawer = (side) => () => {
    console.log('entered toggleDocDrawer')
    setState({ ...state, [side]: !state.left })
  }

  const closeAllDrawers = () => () => {
    console.log('entered closeAll')
    setState({ ...state, left: false })
    setState({ ...state, right: false })
    console.log('doc = ' + state.left)
    console.log('edit = ' + state.right)
  }

  const docPanel = (
    <div className={classes.docContainer}>
      <Typography className={classes.h2}> How to configure </Typography>
      <Button className={classes.btnSecondary} onClick={toggleDrawer('left', false)}>Close</Button>
      <Divider />
      <iframe src="https://docsify.js.org/#/configuration" width="480" height="480"></iframe>
      {/* <iframe src="https://phabricator.internal.tulip.io/w/clienteling/adminconsole/customer_details/" width="540" height="640"></iframe> */}
      <Divider />
    </div>
  )

  const sideList = (
    <div className={classes.list}>
      <Paper className={classes.panelHeader}>
        <Typography className={classes.h2}> {values.featureID} </Typography>
        <Divider />
      </Paper>
      <form className={classes.container} noValidate autoComplete="off">
        <Paper className={classes.formContainer}>
          <Typography className={classes.h3}> Basic Settings </Typography>
          <TextField
            id="featureID"
            label="Feature ID"
            className={classes.textField}
            value={values.featureID}
            onChange={handleChange('featureID')}
            margin="normal"
            variant="outlined"
            disabled
          />
          <TextField
            id="category"
            label="Category"
            className={classes.textField}
            value={values.category}
            onChange={handleChange('category')}
            margin="normal"
            variant="outlined"
          />
          <TextField
            id="name"
            label="Name"
            className={classes.textField}
            value={values.name}
            onChange={handleChange('name')}
            margin="normal"
            variant="outlined"
          />
          <TextField
            id="type"
            label="Type"
            className={classes.textField}
            value={values.type}
            onChange={handleChange('type')}
            margin="normal"
            variant="outlined"
          />
          <TextField
            id="path"
            label="Path"
            className={classes.textField}
            value={values.path}
            onChange={handleChange('path')}
            margin="normal"
            variant="outlined"
          />
        </Paper>
        <Paper className={classes.formContainer}>
          <Typography className={classes.h3}> Advanced Settings </Typography>
          <TextField
            id="outlined-name"
            label="Name"
            className={classes.textField}
            value={values.name}
            onChange={handleChange('name')}
            margin="normal"
            variant="outlined"
          />
          <TextField
            id="outlined-name"
            label="Name"
            className={classes.textField}
            value={values.name}
            onChange={handleChange('name')}
            margin="normal"
            variant="outlined"
          />
        </Paper>
        <Paper hidden={!state.left}>
          <div
            tabIndex={0}
            role="button"
          >
            {docPanel}
          </div>
        </Paper>
      </form>
      <Paper className={classes.panelBottom}>
        <Divider />
        <Button className={classes.btnLink} onClick={toggleDocDrawer('left')}>{ state.left ? 'Hide Docs' : 'View Docs' }</Button>
        <Button className={classes.btnSecondary} onClick={closeAllDrawers()}>Cancel</Button>
        <Button className={classes.btnPrimary} onClick={toggleDrawer('right', false)}>Save</Button>
      </Paper>
    </div>
  )

  return (
    <div className={classes.root}>
      <TopNav />
      <SideNav />
      <main className={classes.content}>
        <Typography className={classes.h1}>Application Module</Typography>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Feature ID</TableCell>
              <TableCell align="right">Enabled</TableCell>
              <TableCell align="right">URL Path</TableCell>
              <TableCell align="right">Config</TableCell>
              <TableCell align="right">Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(row => (
              <TableRow key={row.id}>
                <TableCell component="th" scope="row">
                  {row.name}
                </TableCell>
                <TableCell align="right">{row.calories}</TableCell>
                <TableCell align="right">{row.fat}</TableCell>
                <TableCell align="right">{row.carbs}</TableCell>
                {/* <TableCell align="right"><Button className={classes.btnLink} onClick={toggleDrawer('right', true)}>{row.protein}</Button></TableCell>   */}
                <TableCell align="right"><Button className={classes.btnLink} onClick={openEditPanel()}>{row.protein}</Button></TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Drawer anchor="right" open={state.right}
        >
          <div
            tabIndex={0}
            role="button"
            className={ state.left ? classes.docOpened : classes.docClosed }
          >
            {sideList}
          </div>
        </Drawer>
      </main>
    </div>
  )
}

Applications.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(Applications)
